        slideInfoText: 'ПМЛ – прогрессирующая мультифокальная лейкоэнцефалопатия,  МРТ – магнитно-резонансная томография, ГМ – головной мозг, JC - - вирус Джона Каннингема ​'
      }, {
        id: 5,
        questionTitle: 'В течение двух лет пациент получал терапию натализумабом, обострений за это время отмечено не было, на МРТ головного мозга через год и через 2 года терапии – без динамики.​',
        questionText: 'Пациенту выполнен анализ крови на антитела к JC – титр 0,6.​',
        question: 'Дальнейшая тактика в отношении ПИТРС у пациента?​',
        a: 'Отменить натализумаб, назначить другой препарат второй линии​',
        b: 'Брать анализ крови на антитела к JC каждые 6 месяцев, выполнять МРТ ГМ каждый год',
        c: 'Брать анализ крови на антитела к JC каждые 6 месяцев, выполнять МРТ ГМ каждые 3 месяца',
        d: 'Кровь на антитела к JC не брать, выполнять МРТ ГМ каждый год​',
        answerVar: 'b',
