        b: 'Перевести на расширенный интервал дозирования, ввести вакцину через несколько дней после инфузии, повторную дозу через 3 недели, далее продолжить натализумаб в РИД**​',
        c: 'Отменить натализумаб на 6 недель, далее провести двукратную вакцинацию, затем через 2 недели возобновить препарат​',
        d: 'Не вакцинировать и выдать отвод по медицинским показаниям​',
